@import url(https://fonts.googleapis.com/css2?family=Lexend+Deca:wght@100;200&display=swap);
@-webkit-keyframes slidein{from{width:100%;left:0}to{width:0;left:100%}}@keyframes slidein{from{width:100%;left:0}to{width:0;left:100%}}*{margin:0;padding:0}.App{display:grid;justify-content:center;grid-gap:30px;gap:30px;width:600px;margin:50px auto;font-size:18px;font-family:"Lexend Deca", sans-serif;font-weight:100}.Clock{display:grid;grid-template-columns:299px 400px 299px;grid-gap:2px;gap:2px;height:100px;justify-content:center;text-align:center;line-height:100px;background-color:#ebebeb;border-radius:2px;color:#ebebeb;box-shadow:5px 4px 8px -5px rgba(0,0,0,0.94)}.Clock__name{display:block;position:relative;text-transform:uppercase;background-color:#251933;font-weight:100;font-size:1.7em}.Clock__name::after{content:"";display:block;position:absolute;top:0;width:0;height:100px;background-color:#251933}.Clock__name__slider::after{-webkit-animation-name:slidein;animation-name:slidein;-webkit-animation-direction:normal;animation-direction:normal;-webkit-animation-duration:1s;animation-duration:1s}.Clock__buttons-container{display:grid;grid-gap:1px;gap:1px}.Clock__button{display:block;width:100%;height:50px;font-family:"Lexend Deca", sans-serif;font-size:1.1em;color:#ebebeb;text-transform:uppercase;background-color:#251933;border:none;cursor:pointer}.Clock__button:hover{color:#fd7506;background-color:#1a1122}.Clock__button:active{font-size:1.07em;color:#b35000}.Clock__button__setName{background-color:#ebebeb;color:#251933;text-transform:lowercase;font-weight:200;border-radius:2px;box-shadow:5px 4px 8px -5px rgba(0,0,0,0.4)}.Clock__button__setName:hover{background-color:rgba(37,25,51,0.7);transition:all 0.15s}.Clock__button__setName:active{background-color:#d4d4d4}.Clock__face-container{width:400px;margin:0 auto;-webkit-perspective:100000px;perspective:100000px;background-color:#251933;border-bottom:1px solid #ebebeb}.Clock__face-container div{position:absolute;top:0;left:0;width:100%}.Clock__face-container div:nth-child(1){-webkit-transform:rotateX(0deg) translateZ(50px);transform:rotateX(0deg) translateZ(50px)}.Clock__face-container div:nth-child(2){-webkit-transform:rotateX(-90deg) translateZ(50px);transform:rotateX(-90deg) translateZ(50px)}.Clock__face{-webkit-transform-style:preserve-3d;transform-style:preserve-3d;height:100px;transition:all 0.35s;font-size:3.5em}.Clock__face__text{height:100px;background:#251933}.Clock__face__text--off{background:#ebebeb;color:#251933}

